.banner{
    background-size: cover;
    color: white;
    height: 448px;
}
.content{
    padding-left: 15px;

    padding-top: 140px;
    height: 190px;
}
.title
{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 400;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgb(51, 51, 51,0.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    background-color: #e6e6e6;
    color: black;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}
.fade{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}